import React from "react";

type ButtonComponentProps = {

  label: string,
  type: 'button' | 'submit',
  onClick?: () => void

}

const ButtonComponent: React.FC<ButtonComponentProps> = (props) => {

  const { label, type = 'button', onClick } = props;

  return (

    <button type={type} onClick={onClick}>{label ? label : "bouton"}</button>

  );

};

export default ButtonComponent;
